<template>
  <div class="container">
    <div
      v-if="Object.keys(lists).length === 0"
      class="no-task"
      style="width: 800px;"
    >
      <i class="el-icon-info" />
      还没有完成待办事项呢！
    </div>
    <div v-else style="width: 800px;">
      <el-card
        v-for="(item, name, index) in lists"
        :key="index"
        class="box-card"
        shadow="hover"
      >
        <div class="completed-title">{{ name }}</div>
        <div
          class="completed-content"
          v-for="(task, index) in item"
          :key="index"
        >
          {{ task.title }}
        </div>
      </el-card>
    </div>
  </div>
</template>

<script>
import { getCompleted } from "../../api/task";

export default {
  data() {
    return {
      lists: {}
    };
  },
  methods: {
    fetch_data() {
      getCompleted(this.$store.getters.token).then(response => {
        this.lists = response.data;
      });
    }
  },
  created() {
    this.fetch_data();
  }
};
</script>

<style scoped>
.container {
  padding: 100px;
  display: flex;
  justify-content: center;
}

.box-card {
  margin-bottom: 30px;
}

.completed-title {
  font-size: large;
  font-weight: bold;
  padding: 10px 0;
  margin-bottom: 30px;
  border: none;
  border-bottom: 1px solid #dcdfe6;
}

.completed-content {
  margin-bottom: 10px;
  text-decoration: line-through;
}
</style>
